@media all and (min-width : 1024px){
    .navbar{
        position: fixed;
        display: flex;
        width: 100%;
        /* min-width: fit-content; */
        height: 80px;
        justify-content: space-evenly;
        align-items: center;
        font-size:x-large;
        /* background-color: rgb(240, 187, 187); */
        /* color: white; */
        color:rgb(247, 180, 180);
        background-color: #363f52;
        border-bottom: 1px solid rgba(179, 131, 131);
        box-shadow: rgba(179, 131, 131) 0px 25px 20px -20px;
        z-index: 1;
    }
    
    .logo{
        cursor: pointer;
    }
    
    .ul{
        display: flex;
        list-style: none;
    }
    
    #link{
        padding: 0 30px;
        cursor: pointer;
    }
    
    .resumebutton1{
        color: white;
        /* border: none; */
        padding: 10px;
        cursor: pointer;
        border-radius: 10px;
        /* background-color: rgb(202, 110, 110); */
        color: rgba(247, 180, 180);
        font-family: cursive;
        font-size: x-large;
        background-color: #363f52;
        box-shadow: rgba(247, 180, 180) 1.95px 1.95px 2.6px;
    }
    
    .resumebutton1:hover{
        transform: scale(1.1);
        transition: ease-in-out .5s;
    }
    
    /* .navbar {
        position: fixed;
        top: 0;
